.checkbox input + label {
  display: block;
  width: 100%;
  font-weight: 500;
  padding-left: 30px;
  margin: 0;
  color: #333;
  font-size: 1rem;
  position: relative;
}

.checkbox input + label:before, .checkbox input + label:after {
  content: '';
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  width: auto;
  height: auto;
  font-size: 1.5rem;
  width: 24px;
  line-height: 1;
  font-weight: normal;
  color: #333;
  position: absolute;
  top: 0;
  right: unset;
  bottom: unset;
  left: 0;
}

.checkbox input + label:before {
  content: '\f096';
  opacity: 1;
}

.checkbox input + label:after {
  content: '\f046';
  opacity: 0;
}

.checkbox input:checked + label:before {
  opacity: 0;
}

.checkbox input:checked + label:after {
  opacity: 1;
}

