  height: 14px;
  border: 4px solid #e51937;
  position: absolute;
  top: 8px;
  left: 0;
  border-radius: 50px;
  opacity: 0;
  -webkit-transition: all, 0.3s;
  -khtml-transition: all, 0.3s;
  -moz-transition: all, 0.3s;
  -ms-transition: all, 0.3s;
  -o-transition: all, 0.3s;
  transition: all, 0.3s;
}

