.popup-sizechart {
	@include transform(scale3d(0.5,0.5,0.5));
	@include transition(all,0.5s);
	width: 800px;
	@include media-breakpoint-down(md) {
		width: 700px; }
	.popup-title {
		@include fs(28rem);
		font-weight: 300;
		text-transform: uppercase;
		margin-bottom: 20px;
		@include media-breakpoint-down(md) {
			@include fs(24rem);
			margin-bottom: 15px; } }
	.des {
		@include fs(14rem);
		color: darken($white,40%);
		margin-bottom: 25px;
		@include media-breakpoint-down(md) {
			@include fs(13rem);
			font-weight: 300;
			margin-bottom: 15px; } }
	.option-group {
		margin-bottom: 30px;
		@include media-breakpoint-down(md) {
			margin-bottom: 20px; }
		.module-title {
			@include fs(13rem);
			font-weight: 700;
			color: $main;
			border-bottom: 1px solid $main;
			margin-bottom: 20px;
			@include media-breakpoint-down(md) {
				margin-bottom: 15px; } }
		.form-group {
			margin-bottom: 15px;
			@include media-breakpoint-down(md) {
				margin-bottom: 10px; }
			label {
				display: inline-block;
				vertical-align: middle;
				font-weight: 300;
				@include fs(13rem);
				margin-right: 15px;
				text-transform: uppercase;
				@include media-breakpoint-down(md) {
					@include fs(12rem);
					margin-right: 10px; } }
			select, input {
				display: inline-block;
				vertical-align: middle;
				width: 40px;
				@include fs(13rem);
				padding: 3px 5px;
				border: 1px solid darken($white,10%);
				@include bor-rad(3px); }
			input[type="radio"] {
				margin-right: 0;
				width: unset; } }
		.size-choice {
			a {
				display: inline-block;
				padding: 3px 5px;
				@include fs(12rem);
				// +bor-rad(3px)
				margin-right: 5px;
				margin-bottom: 5px;
				color: $main;
				border: 1px solid darken($white,10%);
				&:hover {
					color: $main; }
				&.active {
					background: $main;
					color: $white;
					border-color: $main; } } } }
	.btn-choose {
		padding: 15px 0;
		text-align: center;
		color: $white;
		background: $main;
		text-transform: uppercase;
		display: block;
		&:hover {
			background: lighten($main,10%); } } }
.fancybox-slide--current {
	.popup-sizechart {
		@include transform(scale3d(1,1,1)); } }
// END FANCY BOX

// ECOMERCE
.input-number {
	display: inline-block;
	border: 1px solid darken($white,10%);
	// background: darken($white,2%)
	@include bor-rad(50px);
	&:after {
		content: '';
		display: table;
		width: 100%;
		clear: both; }
	.btn-spin {
		width: 38px;
		height: 38px;
		@include fs(16rem);
		line-height: 38px;
		text-align: center;
		font-weight: 600;
		cursor: pointer;
		float: left; }
	input {
		margin: 0;
		padding: 0;
		float: left;
		border: none;
		line-height: 38px;
		width: 38px;
		text-align: center;
		@include fs(16rem);
		font-weight: 700;
		background: none;
		color: darken($white,60%); } }

