.subscribefrm {
	position: relative;
	.fa.fa-exclamation-triangle {
		color: red;
		@include fs(15rem);
		display: block;
		position: absolute;
		top: 10px;
		right: 15px;
		text-indent: -9999px;
		width: 15px;
		height: 15px;
		line-height: 15px;
		text-align: center;
		&:after {
			content: '\f071';
			font-size: 12px;
			color: red;
			text-indent: 0;
			float: left;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0; }
		@media only screen and (min-width: 1024px) {
			right: 70px;
			top: 13px; } }
	.fa-exclamation-triangle:before, .fa-warning:before {
		margin-right: 5px; } }
.form-group {
	position: relative;
	.fa.fa-exclamation-triangle {
		position: absolute;
		top: 15px;
		right: 15px;
		color: transparent;
		width: 15px;
		height: 15px;
		&:before {
			content: '\f071';
			font-size: 12px;
			font-weight: 500;
			color: red;
