.search-page {
	margin: 40px 0;
	.searchresultsummary {
		display: none; }
	.page-header {
		border: none;
		padding: 0;
		margin: 0; }
	h1 {
		font-size: 21px;
		font-weight: 700;
		margin: 0 0 15px;
		text-transform: none;
		line-height: 1.5em;
		color: $black;
		display: inline-block; }
	.btnSearchWrap {
		position: relative;
		.btnSearch {
			@include opacity(1);
			position: absolute;
			top: 0;
			left: 0;
			text-align: center;
			bottom: 0;
			width: 34px;
			.fa {
				position: absolute;
				right: 0;
				display: block;
				top: 0;
				bottom: 0;
				left: 0;
				margin: 0 auto;
				width: 34px;
				height: 34px;
				line-height: 34px;
				border: 1px solid darken($white, 15%);
				vertical-align: middle; } }
		.seachpage-btn {
			position: relative;
			height: 34px;
			width: 34px;
			@include opacity(0); } }
	.btn {
		background-color: $blue;
		color: $white;
		border: none;
		@include transition(all,.3s);
		&:hover,  &:focus {
			background-color: darken($blue,5%); } }
	.searchsummary {
		margin: 10px 0 25px;
		display: none;
		strong {
			color: $black; } }
	.searchcontrols {
		margin-bottom: 30px;
		input[type="text"] {
			height: 35px;
			line-height: 35px;
			padding: 0 15px;
			border: 1px solid darken($white, 15%);
			font-size: 0.9375rem;
			color: $black;
			@include border-radius(0); }
		input[type="submit"] {
			border: none;
			height: 35px;
			@include bor-rad(0);
			line-height: 35px;
			text-align: center;
			padding: 0 20px;
			color: $white;
			background-color: $pink;
			&:hover {
				background: darken($pink,5%); } } }
	.searchresultlist {
		margin-top: 20px; }
	.searchresult {
		border-bottom: 1px solid darken($white, 15%);
		margin: 0 0 15px;
		padding: 0 0 15px;
		h3 {
			font-size: 16px;
			font-weight: 600;
			margin: 0 0 10px;
			color: $black;
			a {
				color: $blue;
				text-decoration: none;
				&:hover {
					color: $red; } } } }
	hr {
		display: none; } }


// SEARCH SUGGEST
.suggestsearch {
	position: absolute!important;
	top: calc(100% + 3px);
	left: 0;
	width: 100%;
	z-index: 401;
	background: $white;
	box-shadow: 0 3px 30px color-rgba($black,0.2);
	ul {
		margin: 0;
		padding: 0;
		li {
			list-style: none;
			display: block;
			border-bottom: 1px dotted darken($white, 10%);
			&:before, &:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			&:hover,
			&.selected {
				background: darken($white, 10%); }

			&.prdlist_icon {
				margin-bottom: 0;
				height: auto; }
			.product-img {
				width: 70px;
				height: 70px;
				overflow: hidden;
				display: flex;
				justify-content: center;
				align-content: center;
				float: left;
				background: $white;
				border: 1px solid darken($white,10%);
				img {
					max-height: 100%;
					max-width: 100%; } }
			.prd-info {
				width: calc(100% - 70px);
				padding: 10px;
				float: left; }
			a {
				padding: 10px;
				display: block;
				.prd-info {
					text-align: left;
					padding: 0 10px;
					h3,
					h4 {
						@include fs(13rem);
						color: darken($white,40%);
						margin: 0;
						line-height: 1.5; }

					h3 {
						@include fs(15rem);
						color: $pink;
						font-weight: 500; }
					span {
						display: block;
						&.price {
							color: $black; }
						&.price_original {
							color: $black;
							text-decoration: line-through; } } } } } } }

.checkbox {
	input {
		display: none;
		+ label {
			display: block;
			width: 100%;
			font-weight: 500;
			padding-left: 30px;
			margin: 0;
			color: $main;
			@include fs(16rem);
			@include pos(rel,null);
			&:before, &:after {
				content: '';
				margin: 0;
				padding: 0;
				background: none;
				border: none;
				width: auto;
				height: auto;
				@include fs(24rem);
				width: 24px;
				line-height: 1;
				font-weight: normal;
				color: $main;
				@include pos(abs, 0 unset unset 0); }
			&:before {
				content: '\f096';
				opacity: 1; }
			&:after {
				content: '\f046';
				opacity: 0; } }
		&:checked {
			+ label {
				&:before {
					opacity: 0; }
				&:after {
					opacity: 1; } } } }
