.radio {
	input[type="radio"] {
		display: none;
		+ label {
			padding: 5px 0 5px 20px;
			@include pos(rel,null);
			color: darken($white,60%);
			@include transition(all,0.3s);
			&:hover {
				color: $main; }
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				@include pos(abs, 8px null null 0);
				border: 1px solid darken($white,40%); }
			&:after {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				border: 4px solid $red;
				@include pos(abs, 8px null null 0);
				@include bor-rad(50px);
				opacity: 0;
				@include transition(all,0.3s); } }
		&:checked {
			+ label {
				color: $main;
				&:after {
					opacity: 1; } } } } }

