.promotion-page {
	padding: 60px 0;
	@include media-breakpoint-down(md) {
		padding: 40px 0; }
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.promotion-list {
		.promo-card {
			background: darken($white,40%) url(../img/woodbg.png) center center no-repeat;
			background-size: cover;
			margin-bottom: 30px;
			@include bor-rad(5px);
			box-shadow: 0px 5px 10px color-rgba($black,0.3);
			@include transition(all,0.5s);
			&:hover {
				box-shadow: 0px 10px 20px color-rgba($black,0.5); }
			figure {
				@include flexbox;
				flex-wrap: wrap;
				align-items: center;
				align-content: center;
				@include media-breakpoint-down(md) {
					align-items: flex-start;
					align-content: flex-start; }
				.promo-img {
					@include flexbox;
					justify-content: center;
					align-items: center;
					align-content: center;
					width: calc(33.33% - 15px);
					min-height: 280px;
					padding: 35px 0;
					@include media-breakpoint-down(lg) {
						width: calc(40% - 15px);
						align-items: flex-start;
						align-content: flex-start; }
					@include media-breakpoint-down(sm) {
						width: 100%;
						min-height: unset;
						padding: 20px 0; }
					img {
						@include media-breakpoint-down(lg) {
							max-width: 80%;
							max-height: 80%; }
						@include media-breakpoint-down(sm) {
							max-width: 90%;
							max-height: 90%; } } }
				figcaption {
					width: calc(60% - 15px);
					margin-left: 30px;
					@include media-breakpoint-down(lg) {
						padding: 30px 30px 30px 0px; }
					@include media-breakpoint-down(sm) {
						width: 100%;
						padding: 0px 15px 15px;
						margin-left: 0; }
					.promo-name {
						@include fs(24rem);
						font-weight: 900;
						margin-bottom: 15px;
						@include media-breakpoint-down(md) {
							@include fs(20rem); }
						@include media-breakpoint-down(sm) {
							@include fs(18rem);
							margin-bottom: 10px; }
						a {
							color: $red; } }
					.promo-des {
						@include fs(16rem);
						color: $white;
						margin-bottom: 30px;
						@include media-breakpoint-down(md) {
							@include fs(14rem);
							margin-bottom: 20px; }
						@include media-breakpoint-down(sm) {
							@include fs(13rem);
							margin-bottom: 15px; } }
					.btn-order {
						@include fs(16rem);
						text-transform: uppercase;
						color: $white;
						font-weight: 800;
						padding: 12px 25px;
						@include bor-rad(5px);
						background: $blue;
						display: inline-block;
						@include media-breakpoint-down(md) {
							padding: 10px 25px;
							@include fs(14rem); }
						@include media-breakpoint-down(sm) {
							display: block;
							text-align: center;
							@include fs(13rem);
							padding: 10px 25px; }
						&:hover {
							background: linear-gradient(to right,$lred, $red);
							box-shadow: 0px 5px 10px $dred; } } } } } } }
