.page-title {
	@include fs(36rem);
	text-transform: uppercase;
	font-weight: 900;
	color: $menu;
	padding: 10px 0;
	margin-bottom: 30px;
	border-bottom: 2px dotted $menu;
	@include media-breakpoint-down(md) {
		@include fs(28rem); }
	@include media-breakpoint-down(sm) {
		@include fs(24rem); }
	@include media-breakpoint-down(xs) {
		@include fs(18rem); } }
main {
	background: url(../img/mainbg.jpg) center center repeat; }
.home-banner {
	@include pos(rel,null);
	.banner-img {
		width: 100%;
		height: calc(100vh - 110px);
		min-height: 670px;
		// +flexbox
		// justify-content: center
		// align-items: center
		overflow: hidden;
		@include media-breakpoint-down(lg) {
			height: calc(100vh - 100px);
			min-height: unset; }
		@include media-breakpoint-down(md) {
			height: calc(100vh - 118px); }
		@include media-breakpoint-down(sm) {
			height: calc(100vh - 93px); }
		@include media-breakpoint-down(xs) {
			height: calc(100vh - 92px); }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	.banner-nav {
		@include pos(abs, null null 15% 50%);
		@include transform(translate(-50%,0));
		@include flexbox;
		@include media-breakpoint-down(xs) {
			flex-wrap: wrap;
			justify-content: center;
 }			// bottom: 10%
		a {
			display: block;
			padding: 10px;
			background: linear-gradient(to right,$blue, $blue);
			box-shadow: 0px 5px 10px color-rgba($black,0.3);
			&:hover {
				background: linear-gradient(to right,$lblue, $blue);
				box-shadow: 0px 5px 10px $dblue; }
			&:last-child {
				background: linear-gradient(to right,$red,$red);
				margin-left: 40px;
				@include media-breakpoint-down(xs) {
					margin-left: 0;
					margin-top: 20px; }
				&:hover {
					background: linear-gradient(to right,$lred, $red);
					box-shadow: 0px 5px 10px $dred; } }
			.link {
				border: 1px solid $white;
				@include bor-rad(5px);
				@include flexbox;
				align-items: center;
				align-content: center;
				width: 260px;
				height: 90px;
				color: $white;
				@include media-breakpoint-down(lg) {
					width: 230px;
					height: 70px; }
				@include media-breakpoint-down(sm) {
					width: 180px;
					height: 60px; }
				@include media-breakpoint-down(xs) {
					width: 230px; }
				.icon {
					width: 120px;
					@include flexbox;
					justify-content: center;
					align-items: center;
					@include media-breakpoint-down(lg) {
						width: 100px;
						height: 100%;
						img {
							max-width: 70%;
							max-height: 70%; } }
					@include media-breakpoint-down(sm) {
						width: 70px;
						height: 70px; } }
				.text {
					width: calc(100% - 120px);
					@include fs(16rem);
					line-height: 1.5;
					font-weight: 900;
					text-transform: uppercase;
					@include media-breakpoint-down(lg) {
						width: calc(100% - 100px);
						@include fs(14rem); }
					@include media-breakpoint-down(sm) {
						width: calc(100% - 70px);
						@include fs(12rem); } } } } } }
