.booking-page {
	padding: 60px 0;
	@include media-breakpoint-down(md) {
		padding: 40px 0; }
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	&.b1, &.b3 {
		min-height: calc(100vh - 210px);
		background: url(../img/quytrinh/bg2.jpg) center center no-repeat;
		background-size: cover;
		@include media-breakpoint-down(lg) {
			min-height: calc(100vh - 70px - 100px); }
		@include media-breakpoint-down(xs) {
			min-height: calc(100vh - 70px - 92px); }
		.page-title {
			color: $white;
			border-color: $white; } }
	&.b5 {
		min-height: calc(100vh - 210px);
		@include media-breakpoint-down(lg) {
			min-height: calc(100vh - 70px - 100px); }
		@include media-breakpoint-down(xs) {
			min-height: calc(100vh - 70px - 92px); } }
	.booking-method {
		padding-top: 30px;
		margin-bottom: 50px;
		@include media-breakpoint-down(xs) {
			padding-top: 0;
			margin-bottom: 25px; }
		.module-title {
			@include fs(14rem);
			text-align: center;
			color: $white;
			margin-bottom: 25px; }
		.method-list {
			@include flexbox;
			justify-content: center;
			align-items: center;
			align-content: center;
			@include media-breakpoint-down(xs) {
				flex-wrap: wrap; }
			.or {
				width: 40px;
				height: 40px;
				background: $blue;
				line-height: 40px;
				text-align: center;
				@include fs(10rem);
				color: $white;
				margin: 0px 30px;
				@include bor-rad(50px);
				text-transform: uppercase;
				@include media-breakpoint-down(sm) {
					margin: 0px 15px; }
				@include media-breakpoint-down(xs) {
					margin: 15px 30%; } }
			a {
				display: block;
				// width: 280px
				// height: 110px
				padding: 10px;
				box-shadow: 0 5px 10px color-rgba($black,0.3);
				&:first-child {
					background: $blue;
					&:hover {
						background: linear-gradient(to right,$lblue, $blue);
						box-shadow: 0px 5px 10px $dblue; } }
				&:last-child {
					background: $red;
					&:hover {
						background: linear-gradient(to right,$lred, $red);
						box-shadow: 0px 5px 10px $dred; } }
				.link {
					width: 260px;
					height: 90px;
					@include flexbox;
					justify-content: center;
					align-items: center;
					align-content: center;
					border: 1px solid $white;
					@include bor-rad(5px);
					@include media-breakpoint-down(lg) {
						width: 230px;
						height: 70px; }
					@include media-breakpoint-down(sm) {
						width: 200px;
						height: 60px; }
					@include media-breakpoint-down(xs) {
						width: 230px; }
					.icon {
						@include flexbox;
						width: 64px;
						height: 64px;
						justify-content: center;
						align-items: center;
						margin-right: 10px;
						@include media-breakpoint-down(lg) {
							width: 100px;
							height: 100%;
							img {
								max-width: 70%;
								max-height: 70%; } }
						@include media-breakpoint-down(sm) {
							width: 70px;
							height: 70px; } }
					.text {
						@include fs(16rem);
						text-transform: uppercase;
						font-weight: 900;
						color: $white;
						@include media-breakpoint-down(lg) {
							width: calc(100% - 100px);
							@include fs(14rem); }
						@include media-breakpoint-down(sm) {
							width: calc(100% - 70px);
							@include fs(12rem); } } } } } }
	.login-promotion {
		text-align: center;
		.title {
			@include fs(24rem);
			font-weight: 900;
			color: $blue;
			margin-bottom: 15px;
			@include media-breakpoint-down(xs) {
				margin-bottom: 10px;
				@include fs(20rem); } }
		.des {
			color: $white;
			@include fs(14rem);
			a {
				color: $white;
				font-weight: 900;
				&:hover {
					color: $red; } } } }
	.thankyou-content {
		text-align: center;
		max-width: 800px;
		margin: 0 auto 40px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 20px; }
		.title {
			@include fs(14rem);
			font-weight: 900;
			font-style: italic;
			text-transform: uppercase;
			color: $blue;
			margin-bottom: 10px;
			@include media-breakpoint-down(sm) {
				@include fs(13rem); } }
		.content {
			@include fs(14rem);
			font-weight: 500;
			@include media-breakpoint-down(sm) {
				@include fs(13rem); }
			.id {
				color: $red;
				font-weight: 900; } } }
	.btn-bookingwrap {
		text-align: right;
		border-top: 1px solid darken($white,40%);
		padding-top: 20px;
		margin-bottom: 40px;
		.btn-continue {
			@include mainbutton;
			display: inline-block;
			line-height: 40px;
			padding: 0px 40px;
			&:after {
				content: '\f0da';
				margin-left: 5px; } } }
	.btn-finish {
		@include mainbutton;
		display: inline-block;
		line-height: 40px;
		padding: 0px 60px;
		@include media-breakpoint-down(sm) {
			@include fs(14rem);
			line-height: 36px; } } }
.booking-map {
	border: 2px solid $blue;
	box-shadow: 0px 5px 10px color-rgba($black,0.3);
	@include media-breakpoint-down(md) {
		box-shadow: 0px 5px 10px color-rgba($black,0.1);
		margin-bottom: 40px; }
	@include media-breakpoint-down(sm) {
		margin-bottom: 20px; } }
.booking-form {
	.row {
		margin: 0px -10px;
		&.row-b32 {
			margin: 0px -40px;
			@include media-breakpoint-down(lg) {
				margin: 0px -15px; } } }
	.col-form {
		width: 47%;
		padding: 0px 10px;
		@include media-breakpoint-down(lg) {
			width: 45%; }
		@include media-breakpoint-down(md) {
			width: 100%; }
		&.col-form-2 {
			width: 50%;
			padding: 0px 40px;
			@include media-breakpoint-down(lg) {
				padding: 0px 15px; }
			@include media-breakpoint-down(md) {
				width: 100%; } } }
	.col-or {
		width: 6%;
		padding: 0px 10px;
		@include media-breakpoint-down(lg) {
			width: 10%; }
		@include media-breakpoint-down(md) {
			width: 100%; }
		.or-wrap {
			@include flexbox;
			justify-content: center;
			align-items: center;
			align-content: center;
			height: 100%;
			@include media-breakpoint-down(md) {
				padding: 20px 0; }
			.or {
				display: block;
				width: 40px;
				height: 40px;
				background: $blue;
				line-height: 40px;
				text-align: center;
				@include fs(10rem);
				color: $white;
				@include bor-rad(50px);
				text-transform: uppercase; } } }
	.form-title {
		@include fs(24rem);
		font-weight: 900;
		color: $blue;
		margin-bottom: 18px;
		@include media-breakpoint-down(md) {
			margin-bottom: 10px;
			@include fs(20rem); }
		@include media-breakpoint-down(xs) {
			@include fs(18rem); } }
	.form-des {
		@include fs(14rem);
		color: $white;
		margin-bottom: 20px;
		@include media-breakpoint-down(md) {
			margin-bottom: 15px; }
		@include media-breakpoint-down(xs) {
			@include fs(13rem); } }
	.form-wrap {
		margin: 0px -10px;
		.col-12 {
			padding: 0px 10px; }
		.form-group {
			margin-bottom: 15px;
			@include media-breakpoint-down(md) {
				margin-bottom: 10px; } }
		label {
			color: $blue;
			@include fs(14rem);
			font-weight: 900;
			color: $blue;
			margin-bottom: 10px; }
		input, select, textarea {
			width: 100%;
			@include bor-rad(5px);
			background: $white;
			padding: 11px 15px;
			@include fs(14rem);
			border: none;
			&:hover, &:active, &:focus {
				box-shadow: 0px 5px 10px $blue; } }
		select {
			padding: 10px 15px; }
		.btn-wrap {
			padding: 0px 10px;
			margin: 25px 0 0;
			@include media-breakpoint-down(md) {
				margin: 10px 0px 0px; }
			input[type="submit"], .btn-book {
				@include fs(16rem);
				font-weight: 900;
				color: $white;
				background: $red;
				@include bor-rad(5px);
				padding: 0px 30px;
				line-height: 40px;
				margin: 0;
				text-transform: uppercase;
				box-shadow: 0px 5px 10px color-rgba($black,0.3);
				&:after {
					content: '\f0da';
					line-height: 1;
					margin-left: 10px; }
				&:hover {
					box-shadow: 0px 5px 10px $dred;
					background: linear-gradient(135deg, $dred,$red); } } } }
	.btnlogin-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		padding: 0px 10px;
		margin: 30px 0 40px;
		@include media-breakpoint-down(md) {
			margin: 10px 0 20px; }
		@include media-breakpoint-down(xs) {
			width: 100%;
			justify-content: space-between; }
		input[type="submit"], .btn-register {
			display: block;
			width: 150px;
			line-height: 40px;
			padding: 0;
			@include fs(16rem);
			font-family: 'Montserrat', sans-serif;
			@include bor-rad(5px);
			font-weight: 800;
			text-align: center;
			text-transform: uppercase;
			color: $white;
			background: $blue;
			box-shadow: 0px 5px 10px color-rgba($black,0.3);
			@include media-breakpoint-down(xs) {
				width: 140px;
				@include fs(14rem); }
			&:hover {
				background: linear-gradient(135deg, $dblue,$blue);
				box-shadow: 0px 5px 10px $dblue; } }
		.or {
			@include fs(14rem);
			color: $white;
			margin: 0px 25px;
			@include media-breakpoint-down(lg) {
				margin: 0px 10px;
				@include fs(12rem); } } }
	.social-login {
		@include flexbox;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(lg) {
			flex-wrap: wrap; }
		a {
			display: block;
			line-height: 40px;
			flex: 1;
			@include pos(rel,null);
			@include bor-rad(5px);
			@include fs(14rem);
			color: $white;
			padding: 0px 20px 0 60px;
			font-family: 'Montserrat';
			box-shadow: 0px 5px 10px color-rgba($black,0.3);
			@include media-breakpoint-down(lg) {
				flex: unset;
				width: 100%; }
			@include media-breakpoint-down(md) {
				display: block;
				flex: 1; }
			@include media-breakpoint-down(sm) {
				@include fs(13rem);
				padding-left: 50px;
				padding-right: 10px; }
			@include media-breakpoint-down(xs) {
				flex: unset;
				width: 100%; }
			&.fa-facebook {
				background: $facebook;
				&:hover {
					background: linear-gradient(135deg,darken($facebook,20%),$facebook);
					box-shadow: 0px 5px 10px darken($facebook,30%); } }
			&.fa-google-plus {
				background: $google;
				&:hover {
					background: linear-gradient(135deg,darken($google,20%),$google);
					box-shadow: 0px 5px 10px darken($google,40%); } }
			&:before {
				font-family: 'fontawesome';
				width: 40px;
				line-height: 30px;
				height: 30px;
				text-align: center;
				border-right: 1px solid color-rgba($white,0.1);
				@include pos(abs, 5px null null 0); }
			+ a {
				margin-left: 20px;
				@include media-breakpoint-down(lg) {
					margin-left: 0;
					margin-top: 10px; }
				@include media-breakpoint-down(md) {
					margin-top: 0;
					margin-left: 15px; }
				@include media-breakpoint-down(xs) {
					margin-left: 0;
					margin-top: 10px; } } } }
	&.info-form {
		.info-group {
			border-bottom: 2px dotted darken($white,60%);
			padding-bottom: 15px;
			margin-bottom: 25px; }
		.form-wrap {
			input, select, textarea {
				border: 1px solid darken($white,20%);
				box-shadow: inset 0px 2px 5px color-rgba($black,0.3); }
			ul.confirm {
				@include flexbox;
				align-items: center;
				align-content: center;
				.checkbox {
					padding: 7px 0;
					+ .checkbox {
						margin-left: 40px; }
					input {
						+ label {
							display: block;
							width: 100%;
							font-weight: 500;
							padding-left: 30px;
							margin: 0;
							color: $main;
							@include fs(16rem);
							&:before, &:after {
								content: '';
								margin: 0;
								padding: 0;
								background: none;
								border: none;
								width: auto;
								height: auto;
								@include fs(24rem);
								width: 24px;
								line-height: 1;
								font-weight: normal;
								color: $main;
								@include pos(abs, 0 unset unset 0); }
							&:before {
								content: '\f096';
								opacity: 1; }
							&:after {
								content: '\f046';
								opacity: 0; } }
						&:checked {
							+ label {
								&:before {
									opacity: 0; }
								&:after {
									opacity: 1; } } } } } }
			.addr-list {
				.radio {
					label {
						span {
							box-shadow: none; } } } } } }
	.payment-method {
		margin-bottom: 30px;
		@include media-breakpoint-down(md) {
			margin: 0; }
		.row {
			margin: 0px -15px; }
		.method {
			input {
				+ label {
					display: block;
					width: 100%;
					font-weight: 500;
					padding-left: 0;
					margin: 0;
					color: $main;
					@include fs(16rem);
					@include media-breakpoint-down(lg) {
						@include flexbox;
						flex-wrap: wrap;
						align-items: center;
						align-content: center;
						height: 60px;
						@include bor-rad(5px);
						background: $white;
						border: 1px solid darken($white,10%);
						padding-left: 40px;
						margin-bottom: 15px;
						box-shadow: 0px 5px 10px color-rgba($black,0.3); }
					&:before, &:after {
						content: '';
						margin: 0;
						padding: 0;
						background: none;
						border: none;
						width: auto;
						height: auto;
						@include fs(24rem);
						width: 24px;
						line-height: 1;
						font-weight: normal;
						color: $main;
						@include pos(abs, unset unset 0 0);
						@include media-breakpoint-down(lg) {
							bottom: 17px;
							left: 10px; } }
					&:before {
						content: '\f096';
						opacity: 1; }
					&:after {
						content: '\f046';
						opacity: 0; }
					.icon {
						@include flexbox;
						// justify-content: center
						align-items: center;
						align-content: center;
						height: 130px;
						padding-left: 30px;
						// padding: 20px 0
						@include media-breakpoint-down(lg) {
							width: 40px;
							height: 40px;
							justify-content: center;
							padding-left: 0; }
						img {
							@include media-breakpoint-down(lg) {
								max-width: 100%;
								max-height: 100%; } } }
					.text {
						padding-left: 30px;
						font-weight: 500;
						@include media-breakpoint-down(lg) {
							width: calc(100% - 40px); }
						@include media-breakpoint-down(sm) {
							padding-left: 10px;
							@include fs(14rem);
							font-weight: 600; } } }
				&:checked {
					+ label {
						&:before {
							opacity: 0; }
						&:after {
							opacity: 1; } } } } } }
	.payment-des {
		@include fs(14rem);
		font-weight: 500;
		@include media-breakpoint-down(md) {
			margin-bottom: 40px;
			@include fs(13rem); } } }
.booking-bill {
	margin: 60px 0;
	box-shadow: 0px 5px 10px color-rgba($black,0.3);
	@include media-breakpoint-down(md) {
		margin: -40px 0 40px; }
	@include media-breakpoint-down(sm) {
		margin: -40px 0 30px; }
	.voucher {
		background: $menu;
		padding: 20px;
		@include pos(rel,null);
		&:before {
			content: '';
			display: block;
			width: 100%;
			height: 7px;
			background: url(../img/rangcua.png) center bottom repeat-x;
			@include pos(abs, null 0 100% 0); }
		.module-title {
			@include fs(14rem);
			color: $blue;
			font-weight: 900;
			text-transform: uppercase;
			margin-bottom: 15px;
			@include media-breakpoint-down(lg); }
		.voucher-input {
			@include bor-rad(5px);
			overflow: hidden;
			background: $white;
			@include flexbox;
			input {
				background: none;
				margin: 0;
				padding: 0 15px;
				border: none;
				@include fs(14rem);
				line-height: 36px;
				// flex: auto
				width: calc(100% - 36px);
				@include media-breakpoint-down(lg) {
					line-height: 32px;
					@include fs(13rem); } }
			.vouchersubmit {
				// flex: 1
				width: 36px;
				line-height: 36px;
				@include fs(16rem);
				color: $white;
				background: $red;
				padding: 0px 10px;
				@include media-breakpoint-down(lg) {
					@include fs(14rem);
					line-height: 32px; }
				&:hover {
					background: linear-gradient(135deg,$dred,$red); } } } }
	.booking-detail {
		padding: 15px 20px;
		background: $white;
		.title {
			@include fs(16rem);
			font-weight: 900;
			text-transform: uppercase;
			color: $red;
			@include media-breakpoint-down(lg) {
				@include fs(14rem); } }
		.booking-table {
			.product {
				padding: 15px 0;
				border-bottom: 2px dotted darken($white,20%);
				@include flexbox;
				flex-wrap: wrap;
				.product-name {
					width: calc(100% - 100px);
					@include fs(12rem);
					color: $menu;
					line-height: 1.5;
					font-weight: 500;
					margin-bottom: 10px;
					@include media-breakpoint-down(lg) {
						// +fs(11rem)
 } }						// font-weight: 600
				.product-price {
					width: 100px;
					@include fs(15rem);
					font-weight: 900;
					color: $dblue;
					text-align: right;
					@include media-breakpoint-down(lg) {
						@include fs(14rem); } }
				.btn-wrap {
					width: 100%;
					a {
						@include fs(12rem);
						line-height: 1;
						padding: 4px 15px;
						background: darken($white,20%);
						color: $white;
						&.btn-change {
							background: $blue;
							&:hover {
								background: darken($blue,10%); } }
						&.btn-remove {
							margin-left: 10px;
							&:hover {
								background: $red; } } } } } }
		.total-fee {
			padding: 20px 0;
			line-height: 1;
			color: $red;
			@include flexbox;
			align-items: center;
			align-content: center;
			border-bottom: 2px dotted darken($white,20%);
			.label {
				@include fs(14rem);
				font-weight: 900;
				width: 110px;
				text-transform: uppercase;
				@include media-breakpoint-down(lg) {
					@include fs(12rem); } }
			.value {
				width: calc(100% - 110px);
				@include fs(18rem);
				font-weight: 900;
				text-align: right;
				@include media-breakpoint-down(lg) {
					@include fs(16rem); } } }
		.address-info {
			padding: 15px 0;
			.address {
				margin-bottom: 15px;
				&:last-child {
					margin-bottom: 0; }
				.address-name {
					@include fs(13rem);
					font-weight: 900;
					margin-bottom: 5px;
					color: $blue;
					@include media-breakpoint-down(lg) {
						@include fs(12rem); } }
				.address-detail {
					color: $dblue;
					@include fs(13rem);
					font-weight: 500;
					line-height: 1.5;
					@include media-breakpoint-down(lg) {
						@include fs(12rem); } } } } }
	.notice {
		@include fs(15rem);
		font-weight: 600;
		color: $blue;
		&:hover {
			color: $red; }
		&:after {
			content: '\f105';
			margin-left: 5px; } }
	.btnpay-wrap {
		background: $menu;
		padding: 20px;
		@include pos(rel,null);
		&:before {
			content: '';
			display: block;
			width: 100%;
			height: 7px;
			background: url(../img/rangcua2.png) center bottom repeat-x;
			@include pos(abs, 100% 0 null 0); }
		.btn-pay {
			display: block;
			text-align: center;
			line-height: 36px;
			background: $blue;
			color: $white;
			font-weight: 900;
			text-transform: uppercase;
			@include fs(14rem);
			@include bor-rad(5px);
			@include media-breakpoint-down(lg) {
				@include fs(12rem);
				line-height: 32px; }
			&:hover {
				background: linear-gradient(135deg, $dred, $red);
				box-shadow: 0px 5px 10px $dred; } } } }

.booking-progress {
	.step-list {
		@include flexbox;
		justify-content: center;
		max-width: 700px;
		margin: 0 auto 15px;
		padding: 0px 40px;
		.step {
			text-align: center;
			flex: 1;
			@include media-breakpoint-down(sm) {
				display: none;
				&.active {
					display: block; } }
			&.step-3 {
				flex: 2; }
			&.step-5 {
				flex: 3; }
			.step-icon {
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center;
				height: 50px;
				img {} }
			.step-name {
				@include fs(13rem);
				font-weight: 900;
				color: $dblue;
				font-style: italic; } } }
	.progress-list {
		@include flexbox;
		flex-wrap: wrap;
		justify-content: center;
		max-width: 700px;
		margin: 0 auto 40px;
		@include media-breakpoint-down(sm) {
			margin-bottom: 20px; }
		.progress {
			width: 100%;
			margin: 0;
			padding: 0;
			background: none;
			display: none;
			&.active {
				display: block; }
			.progress-img {
				text-align: center;
				img {} } } } }

