.booking-step {
	background: $black;
	.step-list {
		@include flexbox;
		// justify-content: center
		.step {
			border-left: 1px solid $white;
			@include flexbox;
			align-items: center;
			align-content: center;
			@include fs(16rem);
			height: 40px;
			font-weight: 900;
			color: $white;
			font-style: italic;
			width: 190px;
			justify-content: center;
			text-transform: uppercase;
			@include media-breakpoint-down(md);
			@include media-breakpoint-down(sm) {
				width: 40px; }
			&:last-child {
				border-right: 1px solid $white; }
			.number, .name {
				font-weight: 900; }
			.number {}
			.name {
				margin-left: 5px;
				@include media-breakpoint-down(sm) {
					display: none; } }
			&.active {
				background: $red;
				@include media-breakpoint-down(sm) {
					width: calc(100% - 40px - 40px - 40px); }
				.name {
					@include media-breakpoint-down(sm) {
						display: block; } } }
			&.finish {
				.number {
					@include fs(0);
					font-style: normal;
					color: $green;
					&:before {
						content: '\f00c';
						@include fs(18rem); } } } } } }

// PRODUCT POPUP
.popup-wrap {
	@include pos(fix, 0 null 0 0);
	@include flexbox;
	justify-content: center;
	align-items: center;
	align-content: center;
	right: 0;
	z-index: 1500;
	visibility: hidden;
	opacity: 0;
	@include transition(all,0.5s);
	&.open {
		visibility: visible;
		opacity: 1; } }
.popup-overlay {
	@include pos(abs, 0 null 0 0);
	right: 0;
	background: $black;
	opacity: 0.8;
	z-index: 1200; }
.product-popup {
	width: 900px;
	@include pos(rel,null);
	z-index: 1500;
	background: darken($white,40%) url(../img/woodbg.png) center center no-repeat;
	background-size: cover;
	padding: 30px 45px;
	@include transition(all,0.5s);
	@include media-breakpoint-down(md) {
		width: calc(100% - 60px);
		max-height: 90vh;
		overflow: auto;
		padding: 30px; }
	@include media-breakpoint-down(sm) {
		width: calc(100% - 40px);
		max-height: calc(100vh - 40px);
		padding: 20px; }
	@include media-breakpoint-down(xs) {
		width: calc(100% - 40px);
		max-height: calc(100vh - 60px);
		padding: 15px;
		// top: 50%
 }		// +transform(translate(-50%,-50%))
	.btn-closepopup {
		@include pos(abs, -30px 0 null null);
		color: $white;
		cursor: pointer;
		@include media-breakpoint-down(md) {
			top: 10px;
			right: 10px; }
		&:hover {
			color: $red;
			box-shadow: 0px 10px 20px -10px $dred; } }
	.popup-product {
		figure {
			figcaption {
				.product-name {
					@include fs(24rem);
					color: $blue;
					font-weight: 900;
					text-transform: uppercase;
					margin-bottom: 15px;
					@include media-breakpoint-down(sm) {
						@include fs(18rem); } }
				.product-des {
					@include fs(16rem);
					color: $white;
					margin-bottom: 40px;
					@include media-breakpoint-down(sm) {
						@include fs(14rem); } } }
			.product-img {
				@include flexbox;
				justify-content: center;
				@include media-breakpoint-down(sm) {
					height: 150px; }
				img {
					max-width: 100%;
					max-height: 100%; } }
			.product-price {
				@include fs(24rem);
				text-align: center;
				font-weight: 900;
				color: $white;
				font-style: italic;
				@include media-breakpoint-down(sm) {
					@include fs(20rem);
					margin-bottom: 30px; } } } }
	.popup-option {
		.option-group {
			margin-bottom: 15px;
			.title {
				@include fs(16rem);
				font-weight: 900;
				color: $blue;
				margin-bottom: 10px;
				@include media-breakpoint-down(md) {
					@include fs(14rem); } }
			ul.option-list {
				.checkbox {
					margin-bottom: 10px;
					label {
						color: $white;
						@include media-breakpoint-down(md) {
							@include fs(13rem); }
						&:before, &:after {
							color: $white;
							@include media-breakpoint-down(md) {
								top: -3px; } } }
					.option {
						@include flexbox;
						justify-content: space-between;
						.value {
							font-weight: 900; } } } } } }
	.btn-popupwrap {
		@include flexbox;
		// justify-content: space-between
		@include media-breakpoint-down(sm) {
 }			// +pos(abs, null 20px 20px 20px)
		.btn-edit {
			@include mainbutton;
			background: darken($white,40%);
			color: $white;
			line-height: 40px;
			margin-right: 20px;
			padding: 0px 60px;
			&:hover {
				background: linear-gradient(to right, $dblue,$blue);
				box-shadow: 0px 10px 20px -10px $dblue; } }
		.btn-continue {
			@include mainbutton;
			background: $red;
			line-height: 40px;
			padding: 0px 60px;
			@include media-breakpoint-down(xs) {
				width: 100%; } } } }

.confirm-popup {
	@extend .product-popup;
	.confirm-booking {
		.confirm-title {
			@include fs(24rem);
			color: $blue;
			font-weight: 900;
			margin-bottom: 15px;
			text-transform: uppercase;
			@include media-breakpoint-down(md) {
				@include fs(18rem);
				margin-bottom: 10px; } }
		.confirm-des {
			@include fs(16rem);
			color: $white;
			margin-bottom: 20px;
			@include media-breakpoint-down(md) {
				@include fs(14rem);
				margin-bottom: 15px; } }
		.order-detail {
			.group {
				margin-bottom: 20px;
				@include media-breakpoint-down(md) {
					margin-bottom: 14px; } }
			.label {
				@include fs(14rem);
				color: $blue;
				font-weight: 900;
				margin-bottom: 10px;
				@include media-breakpoint-down(md) {
					@include fs(13rem);
					margin-bottom: 6px; } }
			.value {
				@include fs(14rem);
				color: $white;
				@include media-breakpoint-down(md) {
					@include fs(13rem); } }
			.order-table {
				width: 100%;
				margin-bottom: 20px;
				td {
					@include fs(16rem);
					padding: 10px 0;
					border-bottom: 1px solid color-rgba($white,0.1);
					@include media-breakpoint-down(md) {
						@include fs(14rem); }
					&:last-child {
						text-align: right;
						font-weight: 900;
						@include media-breakpoint-down(sm) {
							white-space: nowrap;
							padding-left: 20px; } } } }
			.total-fee {
				text-align: right;
				@include fs(16rem);
				font-weight: 900;
				@include media-breakpoint-down(md) {
					@include fs(14rem); }
				@include media-breakpoint-down(sm) {
					@include fs(12rem); }
				strong {
					@include fs(24rem);
					margin-left: 10px;
					font-weight: 900;
					@include media-breakpoint-down(md) {
						@include fs(20rem); }
					@include media-breakpoint-down(sm) {
						@include fs(16rem); } } } } }
	.btn-popupwrap {
		justify-content: center;
		.btn-edit, .btn-continue {
			@include media-breakpoint-down(sm) {
				padding: 0px;
				@include fs(14rem);
				line-height: 34px;
				text-align: center;
				width: calc(50% - 10px); } } } }
